import { useDispatch, useSelector } from 'react-redux';
import CloseSVG from 'assets/images/close.svg';
import styles from './Settings.module.scss';

function Option({ title, type, value }) {
  const dispatch = useDispatch();
  const { settings } = useSelector((state) => {
    return state;
  });

  function isChecked() {
    return settings.data[type] === value;
  }
  function handleClick() {
    return dispatch({ type: 'settings/data', payload: { key: type, value } });
  }

  return (
    <label className={styles.option}>
      <input type="checkbox" checked={isChecked()} onChange={handleClick} />
      {title}
    </label>
  );
}
function Settings() {
  const dispatch = useDispatch();
  const { settings } = useSelector((state) => {
    return state;
  });

  function handleCloseClick() {
    dispatch({ type: 'settings/hide' });
  }
  function downloadObjectAsJson(exportObj, exportName) {
    const dataStr =
      'data:text/json;charset=utf-8,' +
      encodeURIComponent(JSON.stringify(exportObj));
    const downloadAnchorNode = document.createElement('a');

    exportName += `-${new Date()
      .toLocaleDateString()
      .replaceAll('/', '_')}-${new Date()
      .toLocaleTimeString()
      .substring(0, 8)
      .replaceAll(':', '_')}`;
    downloadAnchorNode.setAttribute('href', dataStr);
    downloadAnchorNode.setAttribute('download', exportName + '.json');
    document.body.appendChild(downloadAnchorNode); // required for firefox
    downloadAnchorNode.click();
    downloadAnchorNode.remove();
  }
  function handleDownload() {
    downloadObjectAsJson(settings.data, 'website_configuration');
  }

  return (
    <div className={styles.container}>
      <div className={styles.header} onClick={handleCloseClick}>
        <img src={CloseSVG} alt="" />
        
        <span className={styles.notice}>
          (         )
        </span>
      </div>
      <hr />
      <div className={styles.content}>
        <div className={styles.section}>
          <div className={styles.title}> :</div>
          <div className={styles.options}>
            <Option title="" type="appLogoSize" value={1} />
            <Option title="" type="appLogoSize" value={2} />
            <Option title="" type="appLogoSize" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>  :</div>
          <div className={styles.options}>
            <Option title="" type="appNameSize" value={1} />
            <Option title="" type="appNameSize" value={2} />
            <Option title="" type="appNameSize" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>  :</div>
          <div className={styles.options}>
            <Option title="" type="appNameColor" value={1} />
            <Option title=" " type="appNameColor" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>  :</div>
          <div className={styles.options}>
            <Option title="" type="menuPosition" value={1} />
            <Option title="" type="menuPosition" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>     :</div>
          <div className={styles.options}>
            <Option title=" " type="menuGroupAnimation" value={1} />
            <Option title=" " type="menuGroupAnimation" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>    :</div>
          <div className={styles.options}>
            <Option
              title="   "
              type="menuGroupPosition"
              value={1}
            />
            <Option title="" type="menuGroupPosition" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>  :</div>
          <div className={styles.options}>
            <Option title="" type="menuGroupType" value={1} />
            <Option title="" type="menuGroupType" value={2} />
            <Option
              title="   "
              type="menuGroupType"
              value={3}
            />
            <Option
              title="     "
              type="menuGroupType"
              value={4}
            />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}> :</div>
          <div className={styles.options}>
            <Option title=" " type="buttonAnimation" value={1} />
            <Option title=" " type="buttonAnimation" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}> :</div>
          <div className={styles.options}>
            <Option title=" " type="backgroundNoise" value={1} />
            <Option title="" type="backgroundNoise" value={2} />
            <Option title="" type="backgroundNoise" value={3} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}> :</div>
          <div className={styles.options}>
            <Option title=" " type="startButton" value={1} />
            <Option title=" " type="startButton" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>      :</div>
          <div className={styles.options}>
            <Option title="" type="showOtcScrollButton" value={1} />
            <Option title=" " type="showOtcScrollButton" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>  ():</div>
          <div className={styles.options}>
            <Option title=" " type="mobileMenuIcon" value={1} />
            <Option title=" " type="mobileMenuIcon" value={2} />
            <Option title=" " type="mobileMenuIcon" value={3} />
            <Option title=" " type="mobileMenuIcon" value={4} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>  ():</div>
          <div className={styles.options}>
            <Option title="" type="mobileSidebar" value={1} />
            <Option title="" type="mobileSidebar" value={2} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>  :</div>
          <div className={styles.options}>
            <Option title=" " type="securityAnimation" value={1} />
            <Option title=" " type="securityAnimation" value={2} />
            <Option title=" " type="securityAnimation" value={3} />
            <Option title=" " type="securityAnimation" value={4} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>  :</div>
          <div className={styles.options}>
            <Option title=" " type="feeAnimation" value={1} />
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.title}>  :</div>
          <div className={styles.options}>
            <Option
              title=" "
              type="globalizationAnimation"
              value={1}
            />
            <Option
              title=" "
              type="globalizationAnimation"
              value={2}
            />
            <Option
              title=" "
              type="globalizationAnimation"
              value={3}
            />
          </div>
        </div>
      </div>
      <div className={styles.footer}>
        <button onClick={handleDownload}> </button>
      </div>
    </div>
  );
}

export default Settings;
